p,
h4 {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.main {
  display: flex;
  flex-wrap: wrap;
  width: 950px;
  margin: auto;
  padding: 50px 0 0;
}

.card {
  box-sizing: border-box;
  width: 200px;
  height: 406px;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;
  margin-right: 48px;
  margin-bottom: 48px;

  &:nth-child(4n) {
    margin-right: 0;
  }

  &:nth-last-child(-n + 4) {
    margin-bottom: 73px;
  }

  &__image {
    display: block;
    width: 160px;
    height: 134px;
    margin: 0 auto;
    margin-bottom: 40px;
  }

  &__productname {
    font-weight: 500;
    font-size: 12px;
    line-height: 18px;
    color: #060b35;
    margin-bottom: 4px;
  }

  &__productcode {
    font-size: 10px;
    line-height: 14px;
    color: #616070;
    margin-bottom: 16px;
  }
}

.stars {
  display: flex;

  &__star {
    background-image: url(/images/star-active.svg);
    background-repeat: no-repeat;
    display: block;
    height: 16px;
    width: 16px;
    margin: 2px;
  }

  &__star:nth-child(5) {
    background-image: url(/images/star.svg);
  }
}

.review {
  display: flex;
  justify-content: space-between;
  // align-items: center;
  margin-bottom: 20px;

  &__text {
    font-size: 10px;
    line-height: 18px;
    color: #060b35;
  }
}

.price {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;

  &__word {
    font-size: 12px;
    line-height: 18px;
    color: #616070;
  }

  &__amount {
    font-weight: bold;
    font-size: 16px;
    line-height: 18px;
    color: #060b35;
  }
}

.button {
  font-family: Roboto, sans-serif;
  display: block;
  width: 166px;
  height: 40px;
  background: #00acdc;
  border-radius: 5px;
  text-align: center;
  text-decoration: none;
  font-weight: bold;
  font-size: 14px;
  line-height: 40px;
  text-transform: uppercase;
  color: #fff;

  &:hover {
    background: #fff;
    border: 1px solid #00acdc;
    box-sizing: border-box;
    border-radius: 5px;
    color: #00acdc;
  }
}
